@import "../bootstrap/variables.less";
@import "../bootstrap/mixins.less";
@import "variables.less";
@import "font.less";
@import "component.less";
@import "common.less";

html, body {
  height: 100%;
  margin: 0;
  background: #f5f5f5;
}

.wrapper {
  min-height: 100%;
  margin: 0 auto -50px;
  padding-bottom: 50px;

  background-color: inherit !important;
}

.content-wrapper {
  background: inherit;
  margin-top: 110px;
}
.no-main-header .content-wrapper {
  margin-top: 0px;
}

.content-box {
  position: relative;
  border: solid 1px #cacaca;
  background: #fafafa;
  width: 100%;
  box-shadow: 0 1px 1px rgba(0, 0, 0, 0.1);
  &.white {
    background: white;
  }
}


.main-footer {
  height: 50px;
  position: relative;
}
.navbar-brand {
  height: auto;
  padding: 10px;
}

.main-header {
  background: #f5f5f5;
  width: 100%;
  max-height: 110px;
  height: 110px;

  position: fixed;
  top: 0px;
  
  & > .navbar {
    margin-left: 0px;
    margin-top: 30px;
    background: #4e433d;
    height: 80px;
  }

  .navbar-header {
    position: relative;
    padding: 0px 0px 0px 60px;
  }

  .navbar-nav > li > a {
    color: white;
    font-size: 14px;
  }

  .navbar-toggle {
    padding: 5px;
    margin: 7px;
  }

  .logo {
    width: auto;
    height: auto;
    
    background-color: transparent !important;
    img.site-logo {
      position: absolute;
      left: 60px;
      top: -24px;
      z-index: 9999;
      height: 100px;
    }
  }
  .logo-slogan {
    margin-left: 110px;
    margin-top: 10px;
    width: 120px;
    display: inline-block; 
    color: white;
    font-size: 14px;
  }

  .navbar-login
  {
      padding: 10px;
      padding-bottom: 0px;
  }

  .notifications-menu {
    .user-notify-link {
      padding-top: 5px; padding-bottom: 5px;
      margin-top: 4px;
      i.fa {
        font-size: 20px;
      }
      span.msg-count {
        position: absolute;
        font-size: 11px;
        line-height: .9;
        top: -2px;
        right: 5px;
        background: red;
        border: solid 2px #4e433d;
        width: 20px;
        height: 20px;
        line-height: 16px;
        text-align: center;
        border-radius: 10px;
        padding: 0px;
      }
    }
  }

  .user-nav {
    .user-nav-link {
      font-size: 18px;
      .caret {
        width: 0;
        height: 0;
        border-style: solid;
        border-width: 12px 8px 0 8px;
        border-color: #f7951d transparent transparent transparent;
        margin-left: 5px;
      }

      img { 
        width: 40px; 
        height: 40px;
        margin-right: 10px;
      }
    }


    li.user-menu-item {
      border-top: solid 1px #e5e5e5;
      a{
        padding: 10px 10px 10px 20px; 
      }
    }
  }

  .navbar-brand > img {
    display: inline-block;
  }
  
  .navbar-login-session
  {
    padding: 10px;
    padding-bottom: 0px;
    padding-top: 0px;
  }

  .icon-size
  {
    font-size: 48px;
  }
}

.fixed .right-side, 
.main-sidebar, .left-side {
  padding-top: 100px;
}

.row-action {
  a.action-link .fa {
    font-size: 1.4em;
    margin: 2px 5px;
  }
}

.dashboard-page {
  .dashboard-section {
    width: 250px;
    margin: 0 auto;
    
    .btn-link {
      display: block;
      margin: 10px 0px;
    }
  }
}

