.cc-injury-quiz {
  .injury-quiz-form {
    .form-material {
      margin-left: 17px;
    }
  }
}

.patient-claim-page .panel.infolist .form-group:last-child {
  padding-bottom: 10px;
}

.learning-center-videos.video-list {
  .video-item {
    
  }
  .video-info {
    min-height: 120px;
  }
  .video-thumbnail {
    img {
      width: 100%;
    }
  }
}

.youtube-video-player {
  display: none;
}

.youtube-video-player.opened {
  display: block;
}

.claim-view-header {
  margin: 10px 0px;
  .claim-info {
    padding-left: 35px;
    padding-right: 35px;
    margin: 5px 0px;

    .claim-doi {
      margin: 5px 0px;
      font-size: 1.2em;
    }
    
    ul.user-info {
      list-style-type: none;
      margin-top: 10px;
      padding: 0px;

      li {
        display: block;
        overflow: hidden;
        i.fa {
          font-size: 14px;
          line-height: 32px;
          width: 16px;
          text-align: center;
          color: #c1c1c1;
          &.fa-mobile-phone {
            font-size: 20px;
          }
          &.fa-map-marker {
            font-size:18px;
          }
          float:left;
        }
        span {
          margin-left: 10px;
          line-height: 32px;
          float:left;
          display: inline-block;
        }
      }
    }
  }

  .claim-tabs {
    border-top: solid 1px #e1e1e1;
    > a {
      padding: 0px 30px;
      border-right: solid 1px #e1e1e1;
      display: block;
      float: left;
      color: #464646;
      margin: 10px 0px;

      &:last-child {
        border-right: none;
      }

      &.claim-activity span {
        background-image: url('../img/claim/activity-feed.png');
      }
      &.claim-docs span {
        background-image: url('../img/claim/docs.png');
      }
      &.claim-photos span {
        background-image: url('../img/claim/photos.png');
      }
      &.claim-others span {
        background-image: url('../img/claim/gear-icon.png');
      }
      span {
        padding-left: 35px;
        display: block;
        line-height: 20px;
        background-position: 0px center;
        background-repeat: no-repeat;
        background-size: contain;
        .text-bold;
      }
    }

    > a.claim-fa-tab {
      span {
        padding-left: 0px;
        i.fa {
          margin-right: 20px;
          color: #bbb;
        }
      }
    }
  }  
}

.claim-page {
  .claim-view-title {
    padding-left: 50px;
  }
}
ul.claim-photo-section {
  padding: 10px 20px;
}

.my-claims-list {
  .claim-item-box {
    margin: 10px 0px 10px 0px;
    padding: 10px 0px 20px;
    width: auto;
  }
  .claim-description {
    padding: 10px 40px;
  }
  .claim-submit-date {
    font-size: 1.3em;
  }
}

.claim-right-panel {
  margin-bottom: 20px;
  font-size: 14px;
  .panel-header {
    padding: 10px 30px;
    background: @color-primary;
    color: white;
  }
  .panel-body {
    padding: 10px 20px 20px;
  }
  &.claim-docs-panel {
    ul {
      list-style-type: none;
      padding-left: 0px;
    }
    .claim-doc-item {
      padding: 10px 10px 10px 20px;
      border-bottom: solid 1px #e1e1e1;
      word-break: break-all;
    }
  }
  &.claim-photos-panel {
    .claim-photo-item {
      width: 33%;
      &>a {
        width: 95%;
        margin: 10px auto;
        border: solid 1px #eee;
        display: block;
        &>img {
          width: 100%;
          height: auto;
        }
      }
    }
  }
}


/* smaller than Small screen */
@media (max-width: (@screen-sm-max)) {
  .claim-right-panel {
    &.claim-photos-panel {
      .claim-photo-item {
        width: 50%;
      }
    }
  }
}

@media (min-width: (@screen-sm-min)) {
  .main-header {
    .navbar-login {
      width: 305px;
    }
    .navbar .top-navbar {
      .navbar-nav {
        float: right;
      }
      .user-nav {
        margin-top: 5px;
      }

      .nav > li > a {
        padding-top: 5px;
        padding-bottom: 5px;
        margin: 0px; 
        
        &:hover {
          background: inherit;
          color: #ccc;
        }
      }

      .top-menu-nav {
        margin: 15px 0px 0px;
        > ul > li {
          border-right: solid 1px #fff;
          > a {
            padding-top: 0px;
            padding-bottom: 0px;
          }

          &:last-child {
            border-right: none;
          }
        }
      }
    }
  }

  .content {
    padding-left: 0px;
    padding-right: 0px;
  }

  .claim-view-header {
    .claim-info {
      .section-1 {
        min-height: 150px;
        border-right: solid 1px #e1e1e1;
      }

      .claim-description {
        width: 80%;
      }
    }
  }

  .my-claims-list {
    .claim-item-box {
      margin-left: 20px;
    }
    .claim-description {
      min-height: 150px;
    }
  }
}

/* smaller than Extra small screen */
@media (max-width: (@screen-xs-max)) {
  .fixed .right-side, 
  .main-sidebar, .left-side {
    padding-top: 70px;
  }

  .navbar-brand {
    padding: 5px 10px;
    img.site-logo {
      height: 60px;
    }
  }

  .main-header {
    height: 70px;
    & > .navbar {
      height: 40px;
      min-height: 40px;
    }

    .logo {
      img.site-logo {
        height: 60px;
      }
    }

    .container {
      & > .navbar-header, 
      & > .navbar-collapse {
        margin: 0px;
        z-index: 99998;
      }
    }

    .navbar-collapse {
      background: #4e433d;
      .navbar-nav > li > a {
        padding: 10px 20px;
      }
    }
  }

  .navbar-nav .notifications-menu.open .dropdown-menu {
    right: -45px;
  }

  .content-wrapper {
    margin-top: 60px;
  }
  .no-main-header .content-wrapper {
    margin-top: 0px;
  }

  .claim-view-header {
    .claim-tabs {
      border-top: solid 1px #e1e1e1;
      > a {
        float: none;
        padding: 5px 50px;
      }
    }
  }

  ul.claim-photo-section {
    padding: 10px 30px;
  }

  .claim-right-panel {
    &.claim-photos-panel {
      .claim-photo-item {
        width: 33%;
      }
    }
  }
}

/* smaller than 640px screen */
@media (max-width: (639px)) {
  
}

/* smaller than 480px screen */
@media (max-width: (479px)) {
  .claim-right-panel {
    &.claim-photos-panel {
      .claim-photo-item {
        width: 50%;
      }
    }
  }
}
